<template>
    <div>
        <span></span>
        <div>
            <!-- <h2>浏览器环境下</h2> -->
            <h3>执行栈与消息队列</h3>
            <ul>
                <li>同步事件依次进入执行栈执行</li>
                <li>异步事件的回调结果进入消息队列中，当前执行栈为空时再将消息队列中的事件放入执行栈执行</li>
                <li>以上步骤循环的过程即事件循环</li>
            </ul>

            <h3>异步事件</h3>
            <p>消息队列中的任务可以分为宏任务和微任务，同一次事件循环中，微任务执行总是先于宏任务</p>
            <h4>宏任务（Macro Task）</h4>
            <ul>
                <li>setInterval()</li>
                <li>setTimeout()</li>
            </ul>

            <h4>微任务（Micro Task）</h4>
            <ul>
                <li>new Promise()</li>
            </ul>
        </div>

    </div>
</template>

